"use strict"

import TypewriterSum from "./typewriter-sum.js";

export function converterSum(user_value, currency, statusVAT) {
    // Фрагмент кода заменяет первую запятую на точку.
    if (user_value.includes(",")) {
        user_value = user_value.split(",").join(".");
    }
    // Фрагмент кода удаляет пробелы.
    user_value = user_value.replace(/\s/g, x => "");

    // Проверка на допустимость вводимого значения.
    if (!TypewriterSum.checkUserSum(currency, user_value)) {
        return false;
    }


    // Основной код функции.
    let amount;
    let VAT;
    let amountVAT;
    user_value = Math.round10(user_value, -2).toString();

    if (statusVAT === "1") {
        // Выделение НДС
        VAT = Math.round10(user_value - user_value / 1.2, -2).toString();
        amount = new TypewriterSum(currency, ...user_value.split("."));
    } else {
        // Начисление НДС
        VAT = user_value * 0.2;
        user_value = Math.round10(Number(user_value) + VAT, -2).toString();
        VAT = Math.round10(VAT, -2).toString();
        amount = new TypewriterSum(currency, ...user_value.split("."));
    }
    amountVAT = new TypewriterSum(currency, ...VAT.split("."));


    // Возвращение результата в текстовом оформлении суммы.
    return [`${amount.distributeFirst()} (${amount}) ${amount.cur_first} ${amount.second} ${amount.cur_second},`
    + " в том числе НДС 20% в сумме " +
    `${amountVAT.distributeFirst()} (${amountVAT}) ${amountVAT.cur_first} ${amountVAT.second} ${amountVAT.cur_second}.`,
        // Сверху запись в договор снизу запись в счет
        `${amount} ${amount.cur_first} ${amount.second} ${amount.cur_second},`
        + " в том числе НДС 20% - " +
        `${amountVAT} ${amountVAT.cur_first} ${amountVAT.second} ${amountVAT.cur_second}.`];
}

(function () {
    /**
     * Корректировка округления десятичных дробей.
     *
     * @param {String}  type  Тип корректировки.
     * @param {Number}  value Число.
     * @param {Integer} exp   Показатель степени (десятичный логарифм основания корректировки).
     * @returns {Number} Скорректированное значение.
     */
    function decimalAdjust(type, value, exp) {
        // Если степень не определена, либо равна нулю...
        if (typeof exp === 'undefined' || +exp === 0) {
            return Math[type](value);
        }
        value = +value;
        exp = +exp;
        // Если значение не является числом, либо степень не является целым числом...
        if (isNaN(value) || !(typeof exp === 'number' && exp % 1 === 0)) {
            return NaN;
        }
        // Сдвиг разрядов
        value = value.toString().split('e');
        value = Math[type](+(value[0] + 'e' + (value[1] ? (+value[1] - exp) : -exp)));
        // Обратный сдвиг
        value = value.toString().split('e');
        return +(value[0] + 'e' + (value[1] ? (+value[1] + exp) : exp));
    }

    // Десятичное округление к ближайшему
    if (!Math.round10) {
        Math.round10 = function (value, exp) {
            return decimalAdjust('round', value, exp);
        };
    }
    // Десятичное округление вниз
    if (!Math.floor10) {
        Math.floor10 = function (value, exp) {
            return decimalAdjust('floor', value, exp);
        };
    }
    // Десятичное округление вверх
    if (!Math.ceil10) {
        Math.ceil10 = function (value, exp) {
            return decimalAdjust('ceil', value, exp);
        };
    }
})();